import React, { Component } from "react";
import { connect } from "react-redux";
import classes from "./style.module.css";

class Model extends Component {
  constructor(props) {
    super(props);
    this.state = {
      contain: "images/background/background_998.jpg",
      body: "images/allbody/bodynew.png",
      bikinitop: "images/allbody/bikini_branew.png",
      bikinibottom: "images/allbody/bikini_pantsnew.png",
      model: "images/model/1000new.png",
      feetleft: "images/allbody/feet_high_leftnew.png",
      feetright: "images/allbody/feet_high_rightnew.png",
      //Set phụ kiện cho Model
      feetStyle: "/images/shoes/shoes1.png",
      handbagStyle: "images/handbags/handbag1.png",
      hairStyle: "/images/hairstyle/hairstyle2.png",
      neckLace: "/images/necklaces/necklace1.png",
      backgroundStyle: "/images/background/background1.jpg",
    };
  }

  render() {
    return (
      <div
        className={classes.contain}
        style={{ background: `url(${this.state.contain})` }}
      >
        <div
          className={classes.body}
          style={{ background: `url(${this.state.body})` }}
        ></div>
        <div
          className={classes.model}
          style={{ background: `url(${this.state.model})` }}
        ></div>
        <div
          className={classes.bikinitop}
          style={{ background: `url(${this.state.bikinitop})` }}
        ></div>
        <div
          className={classes.bikinibottom}
          style={{ background: `url(${this.state.bikinibottom})` }}
        ></div>
        <div
          className={classes.feetleft}
          style={{ background: `url(${this.state.feetleft})` }}
        ></div>
        <div
          className={classes.feetright}
          style={{ background: `url(${this.state.feetright})` }}
        ></div>
        {/* Set quần áo cho Model  */}
        <div
          className={classes.bikinitop}
          style={{
            backgroundImage: `url(${this.props.getCloth.topclothes})`,
            backgroundSize: "cover",
          }}
        ></div>
        <div
          className={classes.bikinibottom}
          style={{
            backgroundImage: `url(${this.props.getCloth.botclothes})`,
            backgroundSize: "cover",
          }}
        ></div>
        <div
          className={classes.feetStyle}
          style={{
            backgroundImage: `url(${this.props.getCloth.shoes})`,
            backgroundSize: "cover",
          }}
        ></div>
        <div
          className={classes.handbagStyle}
          style={{
            backgroundImage: `url(${this.props.getCloth.handbags})`,
            backgroundSize: "cover",
          }}
        ></div>
        <div
          className={classes.hairStyle}
          style={{
            backgroundImage: `url(${this.props.getCloth.hairstyle})`,
            backgroundSize: "cover",
          }}
        ></div>
        <div
          className={classes.neckLace}
          style={{
            backgroundImage: `url(${this.props.getCloth.necklaces})`,
            backgroundSize: "cover",
          }}
        ></div>
        <div
          className={classes.backgroundStyle}
          style={{
            backgroundImage: `url(${this.props.getCloth.background})`,
            backgroundSize: "cover",
          }}
        ></div>
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  getCloth: state.model,
});

export default connect(mapStateToProps)(Model);
