import { combineReducers } from "redux";
import CategoryReducer from "./CateReducer";
import ChooseTypeReducer from "./ChooseTypeReducer";
import ClothesReducers from "./ClothesReducer";
import ModelReducer from "./ModelReducer";

const RootReducer = combineReducers({
  //Danh sách các state lưu trữ trên Store
  tryClothes: ClothesReducers,
  showCategories: CategoryReducer,
  chooseType: ChooseTypeReducer,
  model: ModelReducer,
});

export default RootReducer;
