let modelStore = {
  topclothes: "",
  botclothes: "",
  shoes: "",
  handbags: "",
  necklaces: "",
  hairstyle: "",
  background: "",
};

const ModelReducer = (state = modelStore, action) => {
  switch (action.type) {
    case "SET_CLOTHES":
      console.log(action);
      //Nếu type của state trong component này trùng vs type của data thì code này sẽ gọn hơn
      state[action.payload.type] = action.payload.img;
      // state.topClothes = action.payload.img;
      // state.botClothes = action.payload.img;
      return { ...state };

    default:
      return state;
  }
};

export default ModelReducer;
