import React, { Component } from "react";
import { connect } from "react-redux";

class Categories extends Component {
  render() {
    return (
      <div className="btn-group">
        {this.props.CateList.map((cate, index) => (
          <button
            key={index}
            className={
              this.props.chooseType === cate.type
                ? "btn btn-warning"
                : "btn btn-success"
            }
            onClick={() => this.chooseCategory(cate.type)}
          >
            {cate.showName}
          </button>
        ))}
      </div>
    );
  }

  chooseCategory = (payload) => {
    this.props.dispatch({
      type: "SET_CATEGORY",
      payload: payload,
    });
  };
}

const mapStateToProps = (state) => ({
  CateList: state.showCategories,
  chooseType: state.chooseType,
});

export default connect(mapStateToProps)(Categories);
