import React, { Component } from "react";
import { connect } from "react-redux";

class ProductItem extends Component {
  render() {
    // console.log(this.props.clothProps);
    const { name, imgSrc_jpg } = this.props.clothProps;
    return (
      <div className="card my-3">
        <img src={imgSrc_jpg} alt="imgDress" />
        <p className="text-center text-danger my-3">{name}</p>
        <button
          className="btn btn-info"
          onClick={() => this.chooseClothes(this.props.clothProps)}
        >
          Thử đồ
        </button>
      </div>
    );
  }

  chooseClothes = (item) => {
    this.props.dispatch({
      type: "SET_CLOTHES",
      payload: {
        type: item.type,
        img: item.imgSrc_png,
      },
    });
  };
}

export default connect()(ProductItem);
